body {
  margin: 0;
  position: relative;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  margin-top: 50vmin;
  position: relative;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch__minute {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin-left: -1.5vmin;
  animation: 3600s steps(60, end) infinite clock-hand-rotate;
  position: absolute;
  top: 25%;
  left: 50%;
}

.stopwatch__second {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin-top: 2.1vmin;
  margin-left: -.75vmin;
  animation: 60s linear infinite clock-hand-rotate;
  position: absolute;
  left: 50%;
}

.stopwatch__circle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  margin-right: -50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch__minute--speed-up {
  animation: 600s steps(60, end) infinite clock-hand-rotate;
}

.stopwatch__second--speed-up {
  animation: 10s linear infinite clock-hand-rotate;
}

@keyframes clock-hand-rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

